.App {
  text-align: left;
  box-sizing: border-box;
}
button {
  padding: 5px 10px;
  font-size: 14px;
  color: #fff;
  border-radius: 10px;
  background-color: #000;
}
button + button {
  margin-left: 10px;
}
.black-nav {
  display: flex;
  width: 100%;
  padding: 20px;
  background-color: #000;
  color: #fff;
}
.title {
  padding: 20px;
  font-size: 30px;
  font-weight: 700;
}
div.list {
  padding: 20px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}
